// ==========================================================================
// BASE SPACING
// ==========================================================================

h1,
h2,
h3,
h4,
h5,
h6,
hgroup,
ul,
ol,
dl,
blockquote,
p,
address,
table,
video,
fieldset,
figure,
pre,
hr,
ul,
ol,
.media,
.island,
.islet {
  @include remCalc(margin-bottom, $base-spacing-unit * 1.4);

  @include media(">medium") {
    @include remCalc(margin-bottom, $base-spacing-unit * 1.6);
  }
}

/**
 * Where `margin-left` is concerned we want to try and indent certain elements
 * by a consistent amount. Define that amount once, here.
 */
dd {
  @include remCalc(margin-bottom, $half-spacing-unit);
}

iframe,
.widescreen {
  @include remCalc(margin, $base-spacing-unit, 0, $base-spacing-unit * 2, 0);
}
